import { createStore } from 'vuex'


export const store = createStore({
    state: {
        title: 'Это Vuex',
        products: [
            { id: 1, name: 'iphone13', price: 700},
            { id: 2, name: 'iphone14 pro', price: 1000 },
            { id: 3, name: 'macbook', price: 2000 },
        ],
        users: null
    },
    mutations: {
        addProduct(state, product) {
            state.products.push(product)
        },
        getUsers(state,data) {
            state.users = data
        }
    },
    actions: {
        addProduct(context) {
            setTimeout(() => {
                const product = {
                    id: 4,
                    name: 'airpods',
                    price: 200
                }
                context.commit('addProduct', product)
            }, 2000);
        },
        async getUsers(context) {
            let response = await fetch('https://jsonplaceholder.typicode.com/users')
            let data = await response.json()
            context.commit('getUsers', data)
        }
        
    },
    getters: {
        userOddId(state) {
            if(state.users) {
                return state.users.filter((user) => user.id %2 == 0)
            }
        }
    }
})