<template>
    <div class="container">
        <div class="main__cars" v-if="users">
            <div class="main__cars-item" v-for="user in users" :key="user.id">
                <p>Name: {{ user.name }}</p>
                <p>Email: {{ user.email }}</p>
                <p>City: {{ user.address.city }}</p>
            </div>
        </div>
        <div v-else>
            Идет загрузка...
        </div>
        <p>{{ usersOddId }}</p>
    </div>
</template>

<script>
    export default {
        created() {
          this.getUsers()
        },
        computed: {
            users() {
                return this.$store.state.users
            },
            usersOddId() {
                return this.$store.getters.userOddId
            }
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers')
            }
        },
    }
</script>
